.header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.input-task input {
    margin-bottom: 30px;
    width: 250px;
    height: 30px;
    border-radius: 10px;
    padding: 10px;
    border: none;
    outline: none;
    margin-right: 10px;
}

.input-task [type=text] {
    font-size: 17px;
}

.input-task button {
    width: 50px;
    height: 51px;
    border-radius: 10px;
    outline: none;
    border: none;
    background-color: white;
    transition: 0.5s;
    font-weight: bold;
    font-size: 14px;
}

.input-task button:hover {
    transform: scale(1.05);
}