
.task-style {
    display: flex;
    background-color: white;
    width: 380px;
    height: 40px;
    border-radius: 15px;
    margin-bottom: 30px;
    padding-left: 7px;
    padding-top: 10px;
    padding-right: 20px;
    padding-bottom: 10px;
    justify-content: space-between;
    transition: 0.5s;
}

.task-style:hover {
    transform: scale(1.1);
}

.task-style input {
    margin-top: 10px;
    width: 20px;
}

.task-style p {
    font-size: 18px;
    font-weight: 600;
    margin-top: 10px;
}


.done-task {
    text-decoration: line-through;
    text-decoration-color: #15072d;
}

.task-style button {
    border-radius: 15px;
    outline: none;
    border: none;
    width: 40px;
    height: 40px;
    margin-top: 3px;
    transition: 0.5s;
}

.task-style button:hover {
    transform: scale(1.2);
}

