body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #15072d;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.registration {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.registration h1 {
  color: white;
  margin: 40px;
}

.registration h2 {
  color: white;
  margin-right: 180px;
  margin-bottom: 30px;
}

.inputs {
  display: flex;
  flex-direction: column;
}

.user-name {
  margin-bottom: 30px;
  width: 250px;
  height: 30px;
  border-radius: 10px;
  padding: 10px;
  border: none;
  outline: none;
}

.user-name[type=text] {
  font-size: 17px;
}

.user-pass {
  margin-bottom: 40px;
  width: 250px;
  height: 30px;
  border-radius: 10px;
  padding: 10px;
  border: none;
  outline: none;
}

.user-pass[type=text] {
  font-size: 17px;
}

.singup-button button{
  outline: none;
  border: none;
  padding: 20px;
  border-radius: 60px;
  display: block;
  width: 240px;
  height: 60px;
  font-size: 1.5em;
  box-shadow: 0 10px 0 #15fc89;
  transition: all 0.3;
  font-weight: 600;
}

.singup-button button:hover {
  box-shadow: 0 10px 0 #2ac766;
  transform: translateY(3px);
}

.notification {
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #333;
  color: #fff;
  padding: 10px 20px;
  border-radius: 5px;
  display: none;
}

.hidden {
  display: none;
}

.visible {
  display: block;
}