.full-task {
    display: grid;
}

.full-task textarea {
    height: 320px;
    font-size: 20px;
    font-weight: 600;
    border: none;
    border-radius: 15px;
    padding: 20px;
    resize: none;
    outline: none;
    margin-bottom: 20px;
}

.buttons-edit {
    display: flex;
    justify-content: space-between;
}

.button-apply {
    width: 100px;
    height: 35px;
    border: none;
    outline: none;
    border-radius: 10px;
    background-color: white;
    font-size: 16px;
    font-weight: bolder;
    transition: 0.5s;
}

.button-apply:hover {
    transform: scale(1.08);
}